//获得文章ID
function getUserInfo() {
  var name, value;
  var str = location.href;
  var num = str.indexOf("?");
  var num1 = str.indexOf("#");
  if (num1 != -1) {
    str = str.substring(num + 1, num1);
  } else {
    str = str.substring(num + 1);
  }
  var arr = str.split("&");
  for (var i = 0; i < arr.length; i++) {
    num = arr[i].indexOf("=");
    if (num > 0) {
      name = arr[i].substring(0, num);
      value = arr[i].substr(num + 1);
      this[name] = value;
    }
  }
}
var Request = new getUserInfo();
// let url = 'http://jianmian.myfont.me';
var url = 'http://192.168.248.216:8880';

//自适应屏幕
function fitScreen() {
  function setScale(value) {
    if (window.top !== window) {
      return;
    }
    var pageScale = 1;
    var width = value || 375;
    pageScale = width / 375;
    // meta
    var content = 'width=375, initial-scale=' + pageScale + ', maximum-scale=' + pageScale + ', user-scalable=no';
    document.getElementById('viewport').setAttribute('content', content);
  }
  setScale(document.documentElement.clientWidth);
  window.onorientationchange = function () {
    setTimeout(function () {
      if (window.orientation == 180 || window.orientation == 0) {
        setScale(screen.width);
      }
      if (window.orientation == 90 || window.orientation == -90) {
        setScale(screen.height);
      }
    }, 10);
  };
}
